#root {
	display: grid;
	grid-template-areas: "navigation navigation navigation navigation" "sidebar window window window" "footer footer footer footer";
	grid-template-rows: auto 1fr auto;
}

.navigation {
	grid-area: navigation;
}

.app {}

.sidebar {
	grid-area: sidebar;
}

.window {
	grid-area: window;
	display: grid;
	grid-template-rows: 1fr auto;
}

.footer {
	grid-area: footer;
}